$accent: #0E1734;
$main-color: #192252;
$second-text: #848FAC;

@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
    src: url('../fonts/#{$url}.woff2') format("woff2"),
    url('../fonts/#{$url}.woff') format("woff");
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
  }
}
@include font-face("Inter", "../fonts/Inter-Bold", 700, normal);
@include font-face("Inter", "../fonts/Inter-Regular", 400, normal);
@include font-face("Inter", "../fonts/Inter-Medium", 500, normal);
@include font-face("Montserrat", "../fonts/Montserrat-Bold", 700, normal);
@include font-face("Montserrat", "../fonts/Montserrat-SemiBold", 600, normal);
*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration: none;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
  	overflow-x: hidden;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
